.wrapper {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
}

.wrapper-content {
  display: flex;
}

.header {
  overflow: hidden;
}

.line {
  width: 660px;
  height: 13px;
  background: url("../img/line.png");
  display: block;
  margin: 50px 0;
}

.logo-nav-wrapper {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
}

.logo {
  font-size: 44px;
}

.logo__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #e8603c;
  height: 100%;
  font-weight: bold;
  letter-spacing: 2px;
  margin-left: 1px;
}

.logo__link span {
  color: #f3af9d;
}

.menu__item {
  display: inline-block;
  margin: 17px 0 0 25px;
  letter-spacing: 1px;
}

.menu__link {
  color: #b9b9b9;
  text-decoration: none;
  font-size: 19px;
  font-weight: bold;
}

.menu__link:hover {
  color: #e8603c;
}

.menu__link_active {
  color: #e8603c;
}

.main {
  width: 670px;
}

.block {
  width: 670px;
}

.block__img {
  height: 300px;
  position: relative;
}

.block__img_img1 {
  background-image: url("../img/img1.jpg");
}

.block__img_img2 {
  background-image: url("../img/img2.jpg");
}

.block__img_img3 {
  background-image: url("../img/img3.jpg");
}

.block__ico {
  position: absolute;
  width: 75px;
  height: 75px;
  background-color: #e8603c;
  left: -85px;
  background-position: center center;
  background-repeat: no-repeat;
}

.block__ico_ico1 {
  background-image: url("../img/img.png");
}

.block__ico_ico2 {
  background-image: url("../img/doc.png");
}

.block__ico_ico3 {
  background-image: url("../img/mov.png");
}

.block__content {
  display: flex;
  margin-top: 20px;
}

.block__info {
  width: 100%;
  margin: 2px 0 0 1px;
}

.block__info h4 {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 16px;
}

.block__info p {
  color: #bababa;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin: 7px 0 13px 0;
}

.block__text {
  margin-left: 35px;
}

.block__text h2 {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 21px;
  letter-spacing: 1px;
}

.block__text p {
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin-top: 17px;
  line-height: 17px;
}

.blog {
  height: 61px;
  background: #e8603c;
  margin-top: 16px;
  position: relative;
  margin-bottom: 24px;
}

.blog:before, .blog:after {
  position: absolute;
  content: "";
  height: 61px;
  background: #f2ae9c;
  width: 1000px;
  top: 0;
}

.blog:before {
  right: 100%;
  margin-right: 10px;
}

.blog:after {
  left: 100%;
  margin-left: 10px;
}

.blog__title {
  text-transform: uppercase;
  color: #fff;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 30px;
  padding: 15px 0 0 18px;
  letter-spacing: 1px;
}

.quote {
  width: 654px;
  height: 75px;
  background: url("../img/bg-quote.jpg") #f5f5f5;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-style: italic;
  font-size: 12px;
  color: #474747;
  position: relative;
}

.quote:before {
  position: absolute;
  content: "";
  width: 75px;
  height: 75px;
  background-color: #e8603c;
  background-image: url("../img/quote.png");
  left: -85px;
  background-position: center center;
  background-repeat: no-repeat;
}

.quote blockquote {
  padding: 30px 0 0 80px;
  width: 620px;
  position: relative;
}

.quote blockquote:before {
  position: absolute;
  content: "";
  width: 25px;
  height: 20px;
  background-image: url("../img/quote1.png");
  top: 25px;
  left: 36px;
  background-position: center center;
  background-repeat: no-repeat;
}

.link {
  position: relative;
}

.link:before {
  position: absolute;
  content: "";
  width: 75px;
  height: 75px;
  background-color: #e8603c;
  background-image: url("../img/link.png");
  top: 3px;
  left: -85px;
  background-position: center center;
  background-repeat: no-repeat;
}

.link__title {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 21px;
  letter-spacing: 1px;
}

.link__link {
  color: #bababa;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin: 7px 0 13px 0;
  text-decoration: none;
}

.link__text {
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin-top: 17px;
  line-height: 17px;
}

.buttons {
  display: flex;
}

.buttons__link {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  font-family: 'BebasNeueRegular', sans-serif;
  margin-right: 5px;
}

.buttons__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  background-color: #f3af9d;
}

.buttons__item_active {
  background: #e8603c;
}

.aside {
  width: 220px;
  margin-left: 52px;
}

.a-block {
  width: 100%;
  margin-bottom: 30px;
}

.a-block__title-bg {
  display: flex;
}

.a-block__title {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  white-space: nowrap;
  margin: 2px 15px 0 0;
  letter-spacing: 1.3px;
}

.a-block__bg {
  width: 100%;
  height: 13px;
  background: url("../img/line.png");
  display: block;
  margin-top: 4px;
}

.a-block__text {
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin-top: 17px;
  line-height: 15px;
}

.a-block__link {
  display: block;
  text-decoration: none;
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  line-height: 15px;
}

.a-block__gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
  margin-top: 18px;
}

.a-block__img {
  background-color: #f3af9d;
  border: 1px solid #e8603c;
  width: 50px;
  height: 50px;
}
