.logo-nav-wrapper {
	display: flex;
	justify-content: space-between;
	text-transform: uppercase;
	@include bebas; }

.logo {
	font-size: 44px;

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		text-decoration: none;
		color: $main-color;
		height: 100%;
		font-weight: bold;
		letter-spacing: 2px;
		margin-left: 1px;

		span {
			color: $secondary-color; } } }

.menu {

	&__item {
		display: inline-block;
		margin: 17px 0 0 25px;
		letter-spacing: 1px; }

	&__link {
		color: #b9b9b9;
		text-decoration: none;
		font-size: 19px;
		font-weight: bold;

		&:hover {
			color: $main-color; }

		&_active {
			color: $main-color; } } }

.main {
	width: 670px; }

.block {
	width: 670px;

	&__img {
		height: 300px;
		position: relative;

		&_img1 {
			background-image: url('../img/img1.jpg'); }

		&_img2 {
			background-image: url('../img/img2.jpg'); }

		&_img3 {
			background-image: url('../img/img3.jpg'); } }

	&__ico {
		position: absolute;
		width: 75px;
		height: 75px;
		background-color: $main-color;
		left: -85px;
		background-position: center center;
		background-repeat: no-repeat;

		&_ico1 {
			background-image: url('../img/img.png'); }

		&_ico2 {
			background-image: url('../img/doc.png'); }

		&_ico3 {
			background-image: url('../img/mov.png'); } }

	&__content {
		display: flex;
		margin-top: 20px; }

	&__info {
		width: 100%;
		margin: 2px 0 0 1px;

		h4 {
			color: $main-color;
			text-transform: uppercase;
			@include bebas;
			font-size: 16px; }

		p {
			color: #bababa;
			@include helvetica;
			font-size: 12px;
			margin: 7px 0 13px 0; } }

	&__text {
		margin-left: 35px;
		h2 {
			color: $main-color;
			text-transform: uppercase;
			@include bebas;
			font-size: 21px;
			letter-spacing: 1px; }

		p {
			color: #414141;
			@include helvetica;
			font-size: 12px;
			margin-top: 17px;
			line-height: 17px; } } }

.blog {
	height: 61px;
	background: $main-color;
	margin-top: 16px;
	position: relative;
	margin-bottom: 24px;

	&:before, &:after {
		position: absolute;
		content: "";
		height: 61px;
		background: #f2ae9c;
		width: 1000px;
		top: 0; }

	&:before {
		right: 100%;
		margin-right: 10px; }

	&:after {
		left: 100%;
		margin-left: 10px; }

	&__title {
		text-transform: uppercase;
		color: #fff;
		@include bebas;
		font-size: 30px;
		padding: 15px 0 0 18px;
		letter-spacing: 1px; } }

.quote {
	width: 654px;
	height: 75px;
	background: url('../img/bg-quote.jpg') #f5f5f5;
	@include helvetica;
	font-style: italic;
	font-size: 12px;
	color: #474747;
	position: relative;

	&:before {
		position: absolute;
		content: "";
		width: 75px;
		height: 75px;
		background-color: $main-color;
		background-image: url('../img/quote.png');
		left: -85px;
		background-position: center center;
		background-repeat: no-repeat; }

	blockquote {
		padding: 30px 0 0 80px;
		width: 620px;
		position: relative;

		&:before {
			position: absolute;
			content: "";
			width: 25px;
			height: 20px;
			background-image: url('../img/quote1.png');
			top: 25px;
			left: 36px;
			background-position: center center;
			background-repeat: no-repeat; } } }

.link {
	position: relative;

	&:before {
		position: absolute;
		content: "";
		width: 75px;
		height: 75px;
		background-color: $main-color;
		background-image: url('../img/link.png');
		top: 3px;
		left: -85px;
		background-position: center center;
		background-repeat: no-repeat; }

	&__title {
		color: $main-color;
		text-transform: uppercase;
		@include bebas;
		font-size: 21px;
		letter-spacing: 1px; }

	&__link {
		color: #bababa;
		@include helvetica;
		font-size: 12px;
		margin: 7px 0 13px 0;
		text-decoration: none; }

	&__text {
		color: #414141;
		@include helvetica;
		font-size: 12px;
		margin-top: 17px;
		line-height: 17px; } }

.buttons {
	display: flex;

	&__link {
		display: inline-block;
		text-decoration: none;
		color: #fff;
		@include bebas;
		margin-right: 5px; }

	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 26px;
		height: 26px;
		background-color: $secondary-color;

		&_active {
			background: $main-color; } } }

.aside {
	width: 220px;
	margin-left: 52px; }

.a-block {
	width: 100%;
	margin-bottom: 30px;

	&__title-bg {
		display: flex; }

	&__title {
		color: $main-color;
		text-transform: uppercase;
		@include bebas;
		font-size: 16px;
		letter-spacing: 1px;
		white-space: nowrap;
		margin: 2px 15px 0 0;
		letter-spacing: 1.3px; }

	&__bg {
		width: 100%;
		height: 13px;
		background: url('../img/line.png');
		display: block;
		margin-top: 4px; }

	&__text {
		color: #414141;
		@include helvetica;
		font-size: 12px;
		margin-top: 17px;
		line-height: 15px; }

	&__link {
		display: block;
		text-decoration: none;
		color: #414141;
		@include helvetica;
		line-height: 15px; }

	&__gallery {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-gap: 5px;
		margin-top: 18px; }

	&__img {
		background-color: $secondary-color;
		border: 1px solid $main-color;
		width: 50px;
		height: 50px; } }
